import { Category } from "@/types/types";
const {current} = styles;
type Props = {
  switchCategory: (category: Category | null) => void;
  currentCategory: Category | null;
};
export const CategoryNav = ({ switchCategory, currentCategory }: Props) => {
  const categories: Category[] = [
    "design",
    "cording",
    "wordpress",
    "direction",
    "react/Next.js",
  ];

  return (
    <nav>
      <button onClick={() => switchCategory(null)}>all</button>
      {categories.map((cat) => (
        <button
          onClick={() => switchCategory(cat)}
          key={cat}
          className={currentCategory === cat ? current : ''}
        >
          {cat}
        </button>
      ))}
    </nav>
  );
};
